.page-header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.page-header::after {
  content: "";
  position: absolute;
  z-index: -100;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: $color-dark-blue;
}

.page-header.is-open .page-header__navigation,
.page-header.page-header--nojs .page-header__navigation{
  @include vp-767 {
position: absolute;
right: 0;
top: 0;
background-color: $color-white;
flex-direction: column;
min-width: 275px;
padding-top: 17px;
padding-bottom: 211px;
padding-right: 6px;
padding-left: 15px;
  }
}


.page-header.is-open .main-navigation__logo-link,
.page-header.page-header--nojs .main-navigation__logo-link {
  @include vp-767 {
  color: $color-dark-blue;
  }
}

.page-header.is-open .main-navigation__toggle,
.page-header.page-header--nojs .main-navigation__toggle {
  @include vp-767 {
    display: block;

    &::before {
    position: absolute;
    content: "";
    top: 14px;
    right: 4px;
    width: 19px;
    height: 2px;
    transform: rotate(45deg);
    box-shadow: none;
    background-color: $color-dark-blue;
    }

    &::after {
    position: absolute;
    content: "";
    top: 14px;
    left: 6px;
    width: 19px;
    height: 2px;
    transform: rotate(-45deg);
    box-shadow: none;
    background-color: $color-dark-blue;
    }
  }
}

.page-header.is-open .main-navigation__list,
.page-header.page-header--nojs .main-navigation__list {
  @include vp-767 {
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  align-items: flex-start;
  gap: 26px;
  }
}

.page-header.is-open .site-list__link,
.page-header.page-header--nojs .site-list__link {
  @include vp-767 {
  font-size: 18px;
  line-height: 22px;
  color: $color-dark-blue;
  }
}

.page-header.is-open .main-navigation__logo,
.page-header.page-header--nojs .main-navigation__logo {
  @include vp-767 {
  color: $color-dark-blue;
  }
}

.page-header.is-open .main-navigation__wrapper,
.page-header.page-header--nojs .main-navigation__wrapper {
  @include vp-767 {
  margin-bottom: 39px;
  }
}
